import Vue from 'vue'
import VueRouter from 'vue-router'
import businessLayout from '@/businessLayout/index'
Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    component: businessLayout,
    children: [
      {
        path: '/index', 
        component: () => import('@/views/business/index/index.vue'),
        name: '首页'
      },
      {
        path: '/product', 
        component: () => import('@/views/business/product/product.vue'),
        name: '解决方案'
      },
      {
        path: '/programme', 
        component: () => import('@/views/business/programme/programme.vue'),
        name: '解决方案'
      },
      {
        path: '/system', 
        component: () => import('@/views/business/system/system.vue'),
        name: '系统特色'
      },
      {
        path: '/price', 
        component: () => import('@/views/business/price/price.vue'),
        name: '价格'
      },
      {
        path: '/channel', 
        component: () => import('@/views/business/channel/channel.vue'),
        name: '渠道'
      },
    ]
  },
]

const router = new VueRouter({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes
})

export default router
